:root {
  --working-area: 100%;
  --charcoal: hsla(197, 37%, 24%, 1);
  --persian-green: hsla(173, 52%, 34%, 1);
  --orange-yellow-crayola: hsla(43, 74%, 66%, 0.1);
  --sandy-brown: hsla(27, 87%, 67%, 1);
  --burnt-sienna: hsla(12, 76%, 61%, 1);

  --imperial-red: rgba(230, 57, 70, 1);
  --honeydew: rgba(241, 250, 238, 1);
  --powder-blue: rgba(168, 218, 220, 1);
  --celadon-blue: rgba(69, 123, 157, 1);
  --prussian-blue: rgba(29, 53, 87, 1);

  --background-navbar: #2a9d8f;
  --background-about: #2a9d8f; /* #e74c3c; */
  --background-projects: #e76f51;
  --background-contact: #f4a261;

  --color-navbar: #2a9d8f;

  --chocoyo-green: #276721;
  --chocoyo-dark-green: #0c1b07;
  --chocoyo-yellow: #969c24;
  --flower-orange: #ef6100;
  --screen-size-xs: 0;
  --screen-size-sm: 576px;
  --screen-size-md: 768px;
  --screen-size-lg: 992px;
  --screen-size-xl: 1200px;
  --header-font-family: "Papyrus", "Montserrat", "Sans-Serif";
  --body-font-family: "Montserrat", "Sans-Serif";
  --nav-font-family: "Gill Sans", "Gill Sans MT", "Calibri", "Trebuchet MS",
    "Sans-Serif";
  --fsize: 10px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  position: relative;
}

